	public static &lt;T extends Person> int countPersonWithEmail(ArrayList&lt;T> alist)
	{
		int count=0;
		for(T a: alist)
		{
			if(a.getEmail()!=null)
				count++;
		}
		return count;
	}	
	
	public static &lt;T extends Person> int countPersonWithPhone(ArrayList&lt;T> alist)
	{
		int count=0;
		for(T a: alist)
		{
			if(a.getPhone()!=0L && a.getPhone()!=999999999L)
				count++;
		}
		return count;
	}	
	
	public static int countCustomer(ArrayList&lt;? super Customer> alist)
	{
		int count=0;
		for(Object a: alist)
		{
			if(a instanceof Customer)
				count++;
		}
		return count;
	}	
	
	public static int countExec(ArrayList&lt;? super Exec> alist)
	{
		int count=0;
		for(Object a: alist)
		{
			if(a instanceof Exec)
				count++;
		}
		return count;
	}	
	
	public static &lt;T extends Person> void displayAll(ArrayList&lt;T> alist)
	{
		for(Object a: alist)
		{
			System.out.println(a.toString());
		}
	}	
	
	public static &lt;T extends Person> ArrayList&lt;T> addEmailList(ArrayList&lt;T> alist)
	{
		ArrayList&lt;T> newList = new ArrayList&lt;>();
		for(T a:alist)
			newList.add(a.addEmail());
		return newList;
	}

	
}

